    p.mb-5 Para desarrollar el prototipo de empaques es necesario tener en cuenta:
    TabsA.color-acento-contenido.mb-5
      .tarjeta.color_contenido--claro.p-4(titulo="a. Contenido")
        h3 a. Contenido
        p Determinar cul ser el contenido que va a llevar el empaque, slido, lquido, fluido, pulverizado, esto ayuda a concebir la idea creativa, elegir insumos, sustratos, colores, forma, tamao, peso. Por ejemplo, la gelatina viene en una caja de cartoncillo que a su vez est empacada en una bolsa por sus caractersticas fsicas
      .tarjeta.color_contenido--claro.p-4(titulo="b. Materiales a utilizar")
        h3 b. Materiales a utilizar
        p El empaque protege el producto, al escoger los materiales a utilizar se deben tener en cuenta las propiedades del mismo, especialmente los empaques de alimentos y bebidas que deben cumplir con requerimientos especficos por sus caractersticas de durabilidad, el crecimiento de bacterias, patgenos, duracin de sabor y color. Es fundamental escoger los materiales correctos ya que no es lo mismo un empaque para leche, que uno para atn. 
      .tarjeta.color_contenido--claro.p-4(titulo="c. Tamao y capacidad")
        h3 c. Tamao y capacidad
        p Calcular el tamao de un empaque, suele ser uno de los problemas ms comunes en su diseo. Por lo general se realizan cambios en el prototipo por la dificultad de calcular el volumen de los productos. Por ejemplo, se realiza una caja para zapatos, sin embargo, se debe tener en cuenta que las tallas para adulto van del nmero 34 hasta el 44, aproximadamente. 
      .tarjeta.color_contenido--claro.p-4(titulo="d. Temperatura")
        h3 d. Temperatura
        p Tener presente la temperatura a la cual debe estar un producto dentro de su empaque es importante, ya que puede limitar el diseo y los materiales a utilizar. La temperatura en el interior, puede generar por ejemplo gases que daan el producto como tal, incluso al empaque en algunas ocasiones. 
      .tarjeta.color_contenido--claro.p-4(titulo="e. Complementos")
        h3 e. Complementos
        p Antes de tener en cuenta un prototipo final, es importante saber que complementos van a acompaar al mismo, por ejemplo, el uso de etiquetas o pestaas, esto puede cambiar el diseo por completo.
      .tarjeta.color_contenido--claro.p-4(titulo="f. Ensambles")
        h3 f. Ensambles
        p Algunos empaques tienen ensambles en su construccin, estos procesos pueden requerir tcnicas adicionales en la impresin o materiales a utilizar. 
      .tarjeta.color_contenido--claro.p-4(titulo="g. Recursos")
        h3 g. Recursos
        p El proceso de produccin puede requerir de diversos mtodos y medios tecnolgicos, que deben de estar presente en la concepcin misma del proyecto.
