          b Estructuras: 
          b-none el prototipo de empaque puede ser elaborado en cualquier material y es utilizado para envolver un producto, aunque muchas veces no tenga contacto directo con el mismo y se convierta en una segunda capa que lo envuelve. Generalmente protegen al envase, y a veces tienen una estructura flexible de celofn, papel o tela, por ejemplo, como en el caso de las bolsas, cajas o costales, que pueden contener impresos grficos resultando muchas veces muy atractivos para los clientes y normalmente se le considera un empaque secundario. Sin embargo, el empaque puede tener estructura rgida si es de polietileno, PVC o metalizado, o una estructura semirrgida por ejemplo si es de aluminio
