        h2.text-center Empaque natural
        p.text-center Est compuesto principalmente por materiales o medios amigables con el medio ambiente. Un ejemplo claro de este tipo de empaques se encentra en la misma naturaleza, como la envoltura natural de la mazorca de maz. 
