        h2.text-center Empaque artificial
        p.text-center Es todo aquel que se obtiene a partir de un proceso generado por la transformacin del hombre y cuya materia prima es tomada de la naturaleza. Ejemplo de este tipo de recursos son los cartones, el papel, o los polmeros.
    p.mb-5 Se describen a continuacin, algunas funcionalidades y usos de acuerdo con el tipo de empaque utilizado: 
