    p.mb-5 El empaque protege al producto de los siguientes riesgos:
    .row.mb-3.justify-content-center
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/img92.svg' alt='AvatarTop')
          .tarjeta.color_contenido--claro
            .p-4.text-center
              h4 Mecnicos
              p Protege en el momento de la carga y embalaje al producto, de sobrecargas, golpes y maltrato generado en el almacenamiento y distribucin, deterioro en el set de exhibicin. 
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/img93.svg' alt='AvatarTop')
          .tarjeta.color_contenido--claro
            .p-4.text-center
              h4 Qumicos
              p Protege de contaminacin aerobia o anaerobia de microbios, virus y dems patgenos que puedan estar en el ambiente, y de la reaccin de los componentes de un empaque con otro, por exposicin con diferentes materiales.
    .row.mb-5.justify-content-center
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/img94.svg' alt='AvatarTop')
          .tarjeta.color_contenido--claro
            .p-4.text-center
              h4 Trmicos:
              p Protege de los cambios abruptos de temperatura durante el transporte o exhibicin, y a la exposicin de fro o calor excesivo, a veces por mal funcionamiento de los aparatos electrnicos. 
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/img95.svg' alt='AvatarTop')
          .tarjeta.color_contenido--claro
            .p-4.text-center
              h4 Comerciales
              p Protege durante la exhibicin y promocin del producto en el rea de venta, por maltrato del empaque, pueden ser sustrados, destapados, probados, alterados y manipulados.  
    p.mb-5 El empaque cumple la funcin de proteccin a:
