              p.mb-0
                b Humano: 
                b-none el empaque debe contar con medidas estndar de calificacin que permitan garantizar la estabilidad organolptica del producto, sea un alimento o un producto mdico.
            li.d-flex.mb-3    
              i.fas.fa-square-full.me-2
