                b-none beneficia el trabajo e inversin que realiza el productor, para hacer que sus productos lleguen al mercado, sean apetecidos, valorados y vendidos. 
            li.d-flex.mb-3    
              i.fas.fa-square-full.me-2
              p.mb-0
