        .bloque-texto-b.color-acento-contenido.p-4
          .bloque__bf
            .bloque-texto-b__texto
              h2.mb-0.ms-5 En la industria se utilizan gran diversidad de materiales, especialmente derivados de fibras de celulosa procedentes de la pulpa de la madera.
      .col-lg-6.mb-5.mb-lg-0
        p La impresin flexo grfica, es el mtodo que utiliza una plancha en relieve flexible que permite imprimir sobre diferentes tipos de sustrato; la industria del empaque ha tenido un gran desarrollo, lo que ha hecho que sus costos se reduzcan. En diseo, la seleccin apropiada del sustrato (papel, tela, material de reciclaje, plstico o metal) es muy importante. Se debe tener en cuenta al momento de elegir el sustrato, la terminacin o textura de la superficie, color, gramaje y caractersticas de la impresin. Igualmente, las planchas flexibles fcilmente se adaptan a superficies ligeramente desiguales por lo que se puede imprimir en sustratos como el cartn, sin ejercer mucha presin. Uno de los sustratos que se encuentran en el mercado es el cartn recubierto, que es la materia prima esencial para producir empaques plegables.  
    p.mb-5 El cartn es un sustrato formado por la transposicin de papeles, como en capas, lo que le otorga mayor resistencia y dureza; existen bsicamente cuatro variedades de cartn recubierto: reciclado recubierto, cartn slido blanqueado, kraft no blanqueado recubierto y plegadizo para cajas:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
      div(titulo="1. Cartn Reciclado Recubierto (CRR), o cartn dplex y de respaldo gris. En ingls se conoce como CCN y WLC: ").px-5
        p Esta referencia es la predominante en la industria, es importante destacar que cuando se est imprimiendo es menos flexible que otras variedades. 
          br
          br
          |Es el cartn ms econmico se usa en empaques que no requieran una excelente impresin, resistencia o limpieza, pues se elabora con una mezcla de fibras que han sido recicladas, del papel peridico impreso, cajas o contenedores corrugados, que tienen un recubrimiento multicapa. 
      div(titulo="2. El Cartn Slido Blanqueado o CSB: ").px-5
        p Tradicionalmente esta es la referencia ms costosa, Este tipo de cartn puede tener un pigmento blanco con una capa de estuco que puede ser doble o triple en la parte superior, y una o dos en el reverso. El CSB, en la prensa, ofrece excelente impresin, buenas propiedades de formado y procesado y como su estructura es homognea es ideal para la elaboracin de empaques premium, tiene gran consistencia y no tiene polvo de papel que le genere imperfecciones, por eso se usa en empaques de lujo. As mismo, las fibras delgadas y cortas de esta madera hacen que se obtenga una superficie muy lisa, lo que ayuda a alcanzar empaques lujosos de alta calidad de impresin, se usa en productos cosmticos, farmacuticos, publicidad y artes grficas, instrumentos mdicos y cigarrillos entre otros.
          br
          br
          |Tambin puede combinarse con otros materiales y utilizarse en elaboracin de empaques para lquidos. Se utiliza especialmente para productos que requieran olor y gusto neutro.
      div(titulo="3. Cartn Recubierto No Blanqueado (CRNB) o Kraft, o CNK o SUS en ingls: ").px-5
        p Este cartn es ms econmico que el CSB. Normalmente se fabrica exclusivamente con pasta qumica no blanqueada, de dos o tres capas de estuco en la cara superior, a veces se coloca una capa de estuco en el reverso. Los espesores de este cartn varan entre 14 y 30 puntos, por lo que son utilizados en empaques de alimentos congelados. 
      div(titulo="4. Cartn folding o para cajas plegables o FBB/GC/UC: ").px-5
        p Tambin se conoce como cartn cromo estucado, se fabrica a base de varias capas de fibras vrgenes cortas, gruesas y rgidas Tiene mximo tres capas de estucado en el anverso y en algunos casos tambin est estucado en el reverso. Se utiliza en empaques en que los que la rigidez en el empaque o envase son muy importantes. Sus aplicaciones grficas pueden ser postales y tarjetas o sobres para diferentes ocasiones, as como en empaques de productos de belleza, productos farmacuticos, cigarrillos, alimentos y envases sofisticados para dulces, confites y bebidas.
    p.mb-5 Se listan a continuacin otros materiales diferentes al papel:
