              p Se emplea como material rgido, especialmente para contener tanto elementos slidos como lquidos, ya que permiten un sellamiento total. Estos envases por lo general estn hechos en aluminio y hojalata. Por sus caractersticas fsicas, este tipo de empaques ofrecen una mayor durabilidad y capacidad de conservacin. Entre los productos que se pueden encontrar con este tipo de empaques se encuentran, las gaseosas, aceites, pinturas, dulces, entre muchos otros. 
