    #t_3_4.titulo-segundo
      h2 3.4 Estructuras y diseo
    p.mb-5 Muchos productos por su naturaleza y caractersticas fsicas, necesitan donde reposar. Un buen empaque determina en gran medida el xito o fracaso de un producto, ms all de las cualidades y caractersticas del producto mismo, el primer contacto del cliente es de carcter visual, algo que es aprovechado por el diseo y el marketing en general. 
      br
      br
      |Al crear el prototipo de cualquier empaque, se deben de identificar diversos factores concernientes al diseo y a su funcionalidad; aunque el empaque es lo primero que observa el consumidor, detrs de su desarrollo hay un sinfn de variables y problemticas por resolver. Ms all del diseo, la imagen o la forma, el diseo de empaques requiere incorporar en tiempos modernos una experiencia sensorial.
      br
      br
      | Antes de disear el empaque se debe tener en cuenta:
    .row.justify-content-center.mb-5
      .col-lg-4
        .tarjeta-iconos--azul.p-3.d-flex.align-items-center
          img(src='@/assets/curso/img106.svg' alt='AvatarTop' style='width:78px').me-3
          h4.text-white.mb-0 Caractersticas<br>del producto.
      
      .col-lg-4
        .tarjeta-iconos--azul.p-3.d-flex.align-items-center
          img(src='@/assets/curso/img107.svg' alt='AvatarTop' style='width:78px').me-3
          h4.text-white.mb-0 Finalidad del<br>del producto.
      
      .col-lg-4
        .tarjeta-iconos--azul.p-3.d-flex.align-items-center
          img(src='@/assets/curso/img108.svg' alt='AvatarTop' style='width:78px').me-3
          h4.text-white.mb-0 Medios de transporte y<br>condiciones del mismo.
    p.mb-5 La ingeniera de empaques busca proteger de manera segura los productos, para ser embalados y transportados, estudiando su desarrollo, fallas estructurales y funcionales. Elementos como el uso, la competencia, las estrategias de mercado y los materiales que estn en auge, se tiene en cuenta en la elaboracin del empaque, a partir de estos parmetros bsicos, se empiezan a realizar bocetos y prototipos, los cuales pueden ir cambiando de aspecto durante este proceso. 
    .contenedor-imagen--salida.p-5.mb-5 
      .row.justify-content-center.align-items-center
        .col-lg-3
          img(src='@/assets/curso/img109.svg' alt='AvatarTop')
        .col-lg-5
          h4 Diversas preguntas nacen al inicio de este proceso, algunas de ellas:
          p Cul va a ser su uso?
            br
            | Cul material se va a utilizar?
            br
            | Cules son los procesos que se van a requerir?
            br
            | Cul es el presupuesto y el costo de elaboracin?
            br
            | Cuenta con algn tipo de barrera o proteccin especial? 
    p.mb-5 Aunque estas sean algunas consideraciones de tipo tcnico, se deben tener en cuenta desde el primer momento en el que diseamos, esto con el fin de evitar demoras y sobrecostos en la operacin.
    Separador
    #t_3_5.titulo-segundo
      h2  3.5 Fases de produccin
    p.mb-5 El proceso de produccin grfica consta de muchas variables que hacen parte de la estrategia de produccin, entre ellas se tienen: 
    TabsA.color-acento-contenido.mb-5
      .tarjeta.color_contenido--claro.p-4(titulo="a. Planificacin estratgica")
        .row
          .col-lg-6: img(src='@/assets/curso/img110.jpg' alt='AvatarTop')
          .col-lg-6
            h3 a. Planificacin estratgica
            p El primer paso para el desarrollo de cualquier proyecto en produccin grfica, es el encuentro entre el diseador y el cliente, donde se realiza el brief, documento gua que describe el producto o servicio y su comercializacin. En esta fase es relevante establecer el presupuesto, los costos, sustratos, sistemas de impresin y tiempos establecidos de entrega y desarrollo de las piezas a trabajar.
      .tarjeta.color_contenido--claro.p-4(titulo="b. Propuesta creativa")
        .row
          .col-lg-6: img(src='@/assets/curso/img111.jpg' alt='AvatarTop')
          .col-lg-6
            h3 b. Propuesta creativa
            p Es la propuesta que presenta el diseador como solucin al problema requerido por el cliente, a travs  de bocetos que se ajusten a los parmetros y estndares establecidos, y al presupuesto estipulado en la primera fase de produccin; en algunos casos primero se disea la pieza grfica, para poder tener una idea ms cercana del presupuesto requerido. As mismo, los acabados inciden en el precio, no es lo mismo una pieza que requiere un plastificado que un barnizado.
      .tarjeta.color_contenido--claro.p-4(titulo="c. Artes finales")
        .row
          .col-lg-6: img(src='@/assets/curso/img112.jpg' alt='AvatarTop')
          .col-lg-6
            h3 c. Artes finales
            p Una vez establecidos todos los estndares del proyecto, y aprobados los bocetos, el diseador se dispone a realizar las artes finales con la ayuda de programas de diseo. Es aqu donde aparece el proceso de preimpresin o preprensa, donde se revisa, ajusta y prepara el documento, normalmente en formato PDF para su reproduccin fsica, utilizando un sistema de impresin digital, offset o serigrafa. El arte final asegura que el proyecto sea exitoso o no.
      .tarjeta.color_contenido--claro.p-4(titulo="d. Pruebas de color")
        .row
          .col-lg-6: img(src='@/assets/curso/img113.jpg' alt='AvatarTop')
          .col-lg-6
            h3 d. Pruebas de color
            p Es una simulacin de los colores que pueden ser obtenidos por un sistema de impresin para poder previsualizar el resultado final que se obtendr. Una correcta simulacin del resultado esperado en la impresin permitir realizar cambios de diseo o de color en un archivo antes de proceder a la impresin final (Martnez, s.f.). 
      .tarjeta.color_contenido--claro.p-4(titulo="e. Impresin")
        .row
          .col-lg-6: img(src='@/assets/curso/img114.jpg' alt='AvatarTop')
          .col-lg-6
            h3 e. Impresin
            p En este paso se realiza la reproduccin del documento original sobre papel; sin embargo, existen variedad de materiales sobre los cuales imprimir, siendo necesario emplear diferentes procesos de impresin.
              br
              |Sistemas convencionales de impresin: 
              br
              |Offset
              br
              |Flexografa
              br
              |Tampografa
              br
              |Serigrafa
              br
              br
              |Sistemas digitales de impresin:
              br
              |Inyeccin de tinta
              br
              |Lser
      .tarjeta.color_contenido--claro.p-4(titulo="f. Acabados")
        .row
          .col-lg-6: img(src='@/assets/curso/img115.jpg' alt='AvatarTop')
          .col-lg-6
            h3 f. Acabados
            p Se refiere a los procesos posteriores a la impresin para dar diferentes tipos de terminados al producto, dentro de los que se destacan:
            p.ps-3 Barniz UV, parcial o total, generalmente utilizado en tarjetas, carpetas, revistas, entre otros. 
                br
                br
                |Tintas especiales, para destacar o dar algn tipo de efecto especial.
                br
                br
                |Uso de plastificados, para proteger y dar durabilidad a los elementos, como cuadernos, cartillas, etc.
                br
                br
                |Uso de barnice litogrfico parcial o total. 
                br
                br
                |Troquelados, repujados, efectos con estampados al calor, entre otros.
    Separador
    h3 Requisitos tcnicos y planimetra
    p.mb-5 Como ya se ha mencionado el empaque constituye en una herramienta de mercadeo, distribucin y venta de un producto, pues coadyuva en fortalecer la imagen corporativa, posicionar la marca, influir en la decisin de compra por el consumidor; igualmente, el empaque sirve para proteger el producto, facilitando la manipulacin y la conservacin de ste, preservando su calidad, adems que debe ser funcional, innovador y amigable con el medio ambiente. En general, el empaque est orientado hacia el marketing y deben cumplir con varios requisitos. 
    .row.mb-5 
      .col-lg-6
        p Al tener en cuenta todos estos aspectos, dependiendo del tipo de producto para el que se disee el empaque, es necesario consultar las diferentes normas, que el Instituto Colombiano de Normas Tcnicas y Certificacin (ICONTEC), ha establecido para cada tipo de producto, en lo relacionado con el diseo del empaque, de conformidad con las Normas Tcnicas Colombianas (NTC). As mismo, se deben tener en cuenta las normas expedidas por el Organismo Internacional de Estandarizacin (ISO). Igualmente, la Superintendencia de Industria y Comercio (SIC), as como algunos ministerios que profieren normas relacionadas con el diseo tcnico de los empaques, que es necesario tener en cuenta antes de realizar el diseo respectivo. Todas estas normas deben aplicarse al diseo del empaque, especialmente si se trata de productos para exportar, debido a los altos estndares exigidos por numerosos pases.
      .col-lg-6
        .cajon.color-acento-botones.px-5.py-4
          p Por ejemplo, el Ministerio de Agricultura y Desarrollo Rural y el Ministerio de Comercio, Industria y Turismo, por medio de la Resolucin 0224 de 2007, expidieron el Reglamento Tcnico No. RTC-002 MADR, por medio del cual regularon los requisitos mnimos que deben cumplir los empaques de los productos agrcolas para consumo humano que se importen, se produzcan y se comercialicen en el territorio colombiano.
    p.mb-5 Igualmente, algunos ejemplos relacionados con las normas establecidas por el ICONTEC, en relacin con el tema, son:
    .row.mb-5.justify-content-center
      .col-lg-4
        .tarjeta.color_secundario--claro
            .p-4.text-center
              h4 NTC 1257 
              p Establece los requisitos que deben cumplir las pelculas de polietileno de baja densidad para el empaque de alimentos, debe estar libre de burbujas, de contaminantes, de materias txicas, que no tengan color y establece su ancho mximo.
      .col-lg-4
        .tarjeta.color_secundario--claro
            .p-4.text-center
              h4 NTC 1468 
              p Estableci los requisitos que deben tener los ensayos a los cuales se deben someter los envases de papel o cartn que se usan para envasar la leche, lcteos y jugos. La norma habla del material en que se deben disear los empaques, las tintas a usar para su impresin, espesor y en general el cuidado con la salud de las personas.
      .col-lg-4
        .tarjeta.color_secundario--claro
            .p-4.text-center
              h4 NTC 512-1 y NTC 512-2 
              p Establecen cmo deben ser rotulados o etiquetados en cuanto a sus elementos mnimos, que deben aparecer en los empaques de productos alimenticios.
    p.mb-5 El no cumplimiento de estas normas implica la aplicacin de sanciones y multas y la inconformidad de los clientes, aunada a posibles prdidas econmicas.
      br
      br
      |Por otra parte, el diseo del empaque debe tener en cuenta la etiqueta, cuya funcin es que el consumidor pueda identificar fcilmente el producto, all aparecen adems datos como la fecha de caducidad, informacin o tabla nutricional, instruccin sobre condiciones de uso almacenamiento, nombre y direccin del fabricante, identificacin del lote, etc. En el empaque puede contener el pas de origen, peso del producto, puerto de salida o, de entrada. Igualmente, contendr el cdigo de barras, que es un elemento indispensable, pues permite mantener el inventario actualizado y adems contiene informacin acerca de las caractersticas del producto.
    Separador 
    h3 Preprensa
    p.mb-5 Son todos los pasos anteriores a la impresin, en los cuales se realizan las correcciones y cambios concernientes a un archivo original, para su correcta impresin. En preprensa se revisan, ajustan y aprueban, diferentes componentes como: composicin, dimensiones del documento, textos, paginacin, sangrado de las piezas a imprimir, modo de color CMYK, peso y calidad de las imgenes a utilizar, para as llegar al quemado de planchas, hoy en da se puede dividir la preprensa en convencional y digital.
      br
